import { FORMS_PREVALUESOURCE_ENTITY_TYPE } from "../entities.js";
import type { FormsPrevalueSourceWorkspaceContext } from "./prevaluesource-workspace.context.js";
import { UmbContextToken } from "@umbraco-cms/backoffice/context-api";
import type { UmbWorkspaceContext } from "@umbraco-cms/backoffice/workspace";

export const FORMS_PREVALUESOURCE_WORKSPACE_CONTEXT = new UmbContextToken<
  UmbWorkspaceContext,
  FormsPrevalueSourceWorkspaceContext
>(
  "UmbWorkspaceContext",
  undefined,
  (context): context is FormsPrevalueSourceWorkspaceContext =>
    context.getEntityType?.() === FORMS_PREVALUESOURCE_ENTITY_TYPE,
);
